// Mobile
.checkbox {
  position: relative;
}

.checkbox::before {
  content: "";
  position: absolute;
  background-color: #68b738;

  height: 2px;
  width: 2000px;
  top: 15px;
  // left: 0;
  left: -220px;
  z-index: 1;

  // Tablet
  @media (min-width: @tablet-width) {
    content: "";
    top: 21px;
  }
}

.checkbox__wrapper {
  width: 280px;
  margin: 0 auto;
  margin-bottom: 34px;

  // Tablet
  @media (min-width: @tablet-width) {
    width: 668px;
    margin-bottom: 52px;
  }

  // Desktop
  @media (min-width: @desktop-width) {
    width: 1220px;
    margin-bottom: 60px;
  }
}

.checkbox__title {
  color: #000000;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 24px;
  font-weight: 400;
  line-height: 26px;

  position: relative;
  z-index: 2;

  display: inline-block;
  padding: 0 20px;
  background-color: #ffffff;
  margin: 0 -20px 34px -20px;

  // Tablet
  @media (min-width: @tablet-width) {
    font-size: 36px;
    font-weight: 400;
    line-height: 36px;
    margin-bottom: 57px;
  }
}

.checkbox__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;

  // Tablet
  @media (min-width: @tablet-width) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 32px;
  }

  // Desktop
  @media (min-width: @desktop-width) {
    grid-template-columns: repeat(4, 1fr);
    gap: 80px;
  }
}

.checkbox__item {
  margin-bottom: 15px;

  // Tablet
  @media (min-width: @tablet-width) {
    margin-bottom: 0;
  }
}

.checkbox__item:last-child {
  margin-bottom: 0;
}

.checkbox__control-label {
  color: #444444;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 18px;
  text-transform: uppercase;

  padding-top: 6px;
  padding-bottom: 6px;
}

.checkbox__label {
  position: relative;
  padding-left: 45px;
  display: flex;
  // align-items: center;
}

.checkbox__control {
  position: relative;
}

.checkbox__control[type="checkbox"]:checked + .checkbox__control-mark {
  background-image: url("../img/tick.svg");
  background-position: center;
}

.checkbox__control-mark {
  position: absolute;
  left: 0;
  top: 0;
  width: 30px;
  height: 30px;
  border: 1px solid #cdcdcd;
  background-repeat: no-repeat;
  background-position: 7px 7px;
  background-size: 26px;
}
